@function rem($pixels, $context: $mainFontSize) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }

  @if (unitless($context)) {
    $context: $context * 1px;
  }

  @return $pixels / $context * 1rem;
}

  
@mixin size($width,$height: $width) {
  width: $width;
  height: $height;
}
  
@mixin zero(){
  margin: 0;
  padding: 0;
}

  