.like {
  display: flex;
  flex-shrink: 0;
  box-sizing: border-box;
  width: rem(40);
  height: rem(20);
  vertical-align: middle;
  border: rem(1) solid $Dark25;
  border-radius: rem(10);
}

.like__heart {
  margin-top: rem(6);
  margin-left: rem(5);
}

.like__value {
  box-sizing: border-box;
  margin-left: rem(4);
  color: $Dark25;
  font-weight: normal;
  font-size: rem(10);
  font-family: Montserrat;
  font-style: normal;
  line-height: rem(20);
  text-align: center;
  user-select: none;
}

.heart-liked {
  display: none;
}

.heart_liked {
  border-color: $Purple;

  .like__value {
    color: $Purple;
  }
  .heart-liked {
    display: block;
  }
}
